import React from 'react';
import DeliveryRow, { DeliveryRowProps } from '../DeliveryRow'
import './styles.scss';

interface RowsProps {
  rows: DeliveryRowProps[]
  marginTop?: number
}

const Rows = ({ rows, marginTop}: RowsProps) => {
  return (
    <div className="Order-DeliveryRows-Container" style={{ marginTop }}>
      {rows.map((row, index) => (
        <DeliveryRow key={index} {...row} />
      ))}
    </div>
  )
}

export default Rows