import React from 'react';
import './styles.scss';

interface ButtonProps {
  title: string
  disabled?: boolean
  onClick?: () => void
  style?: React.CSSProperties
}

const Button = ({ title, disabled, onClick, style }: ButtonProps) => {
  return (
    <div 
      className="Order-Button-Container Order-Button-Title" 
      style={{ opacity: disabled ? 0.5 : 1, ...style }}
      onClick={disabled ? () => {} : onClick}
    >
      {title}
    </div>
  )
}

export default Button