import React from 'react';
import './styles.scss';

export interface RowProps {
  title: string
  value: string
  purple?: boolean
  align?: 'left' | 'center' | 'right'
}

const Row = ({ title, value, purple, align = 'left'}: RowProps) => {
  return (
    <div className="Order-Row-Container">
      <p className={`Order-Row-Title text-align-${align}-`}>{title}</p>
      <p className={purple ? `Order-Row-Value-Purple text-align-${align}-` : `Order-Row-Value text-align-${align}-`}>
        {value}
      </p>
    </div>
  )
}

export default Row