import React from 'react';
import './styles.scss';

export interface DeliveryRowProps {
  Icon: any
  value: string
  marginTop?: number
  onClick?: () => void
}

const DeliveryRow = ({ Icon, value, marginTop, onClick }: DeliveryRowProps) => {
  return (
    <div 
      className={`Order-DeliveryRow-Container ${onClick && 'Order-DeliveryRow-Press'}`} 
      style={{ marginTop }} 
      {...{ onClick }}
    >
      <Icon stroke="#8B8B8B" width={26} />
      <p className={`Order-DeliveryRow-Value ${onClick && 'Order-DeliveryRow-Underline'}`}>
        {value}
      </p>
    </div>
  )
}

export default DeliveryRow