import React from 'react';
import Row, { RowProps } from '../Row'
import './styles.scss';

interface RowsProps {
  rows: RowProps[]
  marginTop?: number
}

const Rows = ({ rows, marginTop}: RowsProps) => {
  return (
    <div className="Order-Rows-Container" style={{ marginTop }}>
      {rows.map((row, index) => (
        <Row key={index} {...row} />
      ))}
    </div>
  )
}

export default Rows