import React, { FC, useEffect, useState } from "react";
import { useRouteMatch } from "react-router-dom";
import { Dispatch } from "redux";
import { connect } from "react-redux";
import {
  fetchSingleOrder,
  fetchUpdateOrderStatus,
} from "../../redux/orders/actions";
import { RootState } from "../../redux/RootReducer";
import "../ProductsList/products-list.scss";
import { differenceFormat, parseISODate } from "../../common/utils/date";
import { AcceptDialog } from "../../components/AcceptDialog/AcceptDialog";
import type { RowProps } from "./components/Row";
import type { DeliveryRowProps } from "./components/DeliveryRow";
import { Products, Rows, Button, DeliveryRows, Call } from "./components";
import {
  LocationIcon,
  HomeIcon,
  ProfileIcon,
  CommentIcon,
  PhoneIcon,
  NIFIcon,
} from "../../assets/icons";
import "./order-item.scss";

interface OrderItemProps {
  singleOrder: any;
  user: any;
  fetchSingleOrder: (orderId: string) => void;
  fetchUpdateOrderStatus: (
    orderId: string,
    newStatus: string,
    userId: string
  ) => void;
}

export const STATUS_NAMES: Record<string, string> = {
  "WAIT FOR CARD PAYMENT": "Wait for card payment",
  DELIVERING: "Ready to pick",
  ACCEPTED: "Picking",
  SENT: "Delivering",
  CLOSE: "Delivered",
  CANCELED: "Canceled",
};

const PAYMENT_TYPES: Record<string, string> = {
  CARD: "Card",
  CASH: "Pagar no ato de entrega",
  MBWAY: "MBWAY",
};

const OrderItemContainer: FC<OrderItemProps> = ({
  fetchSingleOrder,
  fetchUpdateOrderStatus,
  singleOrder,
  user,
}) => {
  const match = useRouteMatch<any>();
  const { orderId } = match.params;

  const [confirm, setConfirm] = useState<boolean>(false);

  console.log(singleOrder);

  useEffect(() => {
    fetchSingleOrder(orderId);
  }, []);

  const getDifferencePick = (): string =>
    differenceFormat(
      parseISODate(singleOrder.dateOfAccepted),
      parseISODate(singleOrder.date)
    );

  const getDifferenceClose = (): string =>
    differenceFormat(
      parseISODate(singleOrder.dateOfClose),
      parseISODate(singleOrder.date)
    );

  const getDifferenceDelivered = (): string =>
    differenceFormat(
      parseISODate(singleOrder.dateOfClose),
      parseISODate(singleOrder.dateOfAccepted)
    );

  const checkIsButtonStatusDisabled = () => {
    if (!singleOrder?.status) {
      return true;
    }

    if (user.type === "picker") {
      return (
        singleOrder.status === "SENT" ||
        singleOrder.status === "CLOSE" ||
        singleOrder.status === "CANCELED"
      );
    }

    if (user.type === "dispatcher") {
      return (
        singleOrder.status === "CLOSE" ||
        singleOrder.status === "CANCELED" ||
        singleOrder.status === "DELIVERING"
      );
    }

    return true;
  };

  const disabledStatus = checkIsButtonStatusDisabled();

  const onClickShowMaps = async () => {
    const address = singleOrder.address.streetName.replace(" ", "+");
    const url = `https://www.google.com.ua/maps/place/${address}+${singleOrder.address.streetNumber}+Lisboa`;

    window.open(url);
  };

  const onChangeStatusByRole = () => {
    if (user.type === "picker") {
      fetchUpdateOrderStatus(singleOrder._id, "SENT", singleOrder.userId);
    }

    if (user.type === "dispatcher") {
      fetchUpdateOrderStatus(singleOrder._id, "CLOSE", singleOrder.userId);
    }
  };

  const getTotalOrderPrice = () => {
    if (singleOrder.promocode && singleOrder.promocode.discount) {
      if (
        !singleOrder.promocode.type ||
        singleOrder.promocode.type === "CASH"
      ) {
        const total = singleOrder.price + singleOrder.promocode.discount ?? 0;
        return `${total.toFixed(2)} €`;
      }

      const percent =
        (singleOrder.price * 100) / (100 - singleOrder.promocode.discount);
      return `${percent.toFixed(2)} €`;
    }

    return `${singleOrder.price.toFixed(2)} €`;
  };

  if (!singleOrder) {
    return null;
  }

  const statusRows: RowProps[] = [
    { title: " ", value: " " },
    {
      title: "Status",
      value: STATUS_NAMES[singleOrder.status],
      align: "center",
    },
    { title: " ", value: " " },
  ];

  const priceRows: RowProps[] = [
    {
      title: "Type",
      value: PAYMENT_TYPES[singleOrder.paymentType],
      align: "left",
    },
    { title: "Subtotal", value: getTotalOrderPrice(), align: "center" },
    {
      title: "Total",
      value: `${singleOrder.price?.toFixed(2)} €`,
      align: "right",
      purple: true,
    },
  ];

  const timeRows: RowProps[] = [
    ...(singleOrder?.dateOfAccepted
      ? [{ title: "Picked in", value: getDifferencePick() }]
      : [{ title: " ", value: " " }]),
    ...(singleOrder?.dateOfClose
      ? [{ title: "Delivered in", value: getDifferenceDelivered() }]
      : [{ title: " ", value: " " }]),
    ...(singleOrder?.dateOfClose
      ? [{ title: "Total time", value: getDifferenceClose() }]
      : [{ title: " ", value: " " }]),
  ];

  const nameRows: RowProps[] = [
    ...(singleOrder?.pickedBy
      ? [
          {
            title: "Picked by",
            value: singleOrder?.pickedBy?.name ?? "Name not found",
          },
        ]
      : [{ title: " ", value: " " }]),
    ...(singleOrder?.deliveredBy
      ? [
          {
            title: "Delivered by",
            value: singleOrder?.deliveredBy?.name ?? "Name not found",
          },
        ]
      : [{ title: " ", value: " " }]),
    { title: " ", value: " " },
  ];

  const deliveryRows: DeliveryRowProps[] = [
    ...(singleOrder?.nif && [
      {
        Icon: NIFIcon,
        value: singleOrder.nif,
      },
    ]),
    {
      Icon: LocationIcon,
      value: `${singleOrder.address?.streetName} ${singleOrder.address?.streetNumber}`,
      onClick: onClickShowMaps,
    },
    {
      Icon: HomeIcon,
      value: singleOrder.address?.floor
        ? singleOrder.address?.floor
        : "Not added",
    },
    {
      Icon: CommentIcon,
      value: singleOrder.address?.comment
        ? singleOrder.address?.comment
        : "Not added",
    },
    {
      Icon: PhoneIcon,
      value: singleOrder.phoneNumber,
      marginTop: 40,
      onClick: () => window.open(`tel:${singleOrder.phoneNumber}`),
    },
    {
      Icon: ProfileIcon,
      value:
        singleOrder.userName === "" || singleOrder.userName === " "
          ? "Not added"
          : singleOrder.userName,
    },
  ];

  return (
    <div className="Container-Mobile-Order">
      <p className="Order-Number-Title">
        {singleOrder.orderNumber}
        {singleOrder.status !== "CANCELED" &&
          singleOrder.status !== "WAIT FOR CARD PAYMENT" && (
            <p
              className="Order-Number-Payment-Title"
              style={{
                color:
                  singleOrder.paymentType === "CASH" ? "#FE036A" : "#35C759",
              }}
            >
              {singleOrder.paymentType === "CARD" ||
              singleOrder.paymentType === "MBWAY"
                ? "Paid"
                : "Pagar no ato de entrega"}
            </p>
          )}
      </p>
      {user.type === "picker" && <Products products={singleOrder.products} />}
      {user.type === "dispatcher" && (
        <DeliveryRows rows={deliveryRows} marginTop={30} />
      )}

      <Rows rows={statusRows} marginTop={user.type === "picker" ? 40 : 20} />
      <Rows rows={priceRows} marginTop={9} />

      <Button
        title={user.type === "picker" ? "Picked" : "Delivered"}
        disabled={disabledStatus}
        style={{ marginTop: 40, marginBottom: 50 }}
        onClick={() => setConfirm(true)}
      />

      <Rows rows={timeRows} />
      <Rows rows={nameRows} marginTop={9} />

      {user.type === "dispatcher" && (
        <Products products={singleOrder.products} marginTop={60} />
      )}
      {user.type === "picker" && (
        <DeliveryRows rows={deliveryRows} marginTop={50} />
      )}

      <Call onClick={() => window.open(`tel:${singleOrder.phoneNumber}`)} />

      <AcceptDialog
        title="Change status"
        content="Are you sure you want to change the order status?"
        actionOnSuccess={onChangeStatusByRole}
        show={confirm}
        setShow={setConfirm}
      />
    </div>
  );
};

const mapStateToProps = (state: RootState) => {
  return {
    singleOrder: state.orders.singleOrder,
    user: state.user.user,
  };
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
  fetchSingleOrder: (orderId: string) => dispatch(fetchSingleOrder(orderId)),
  fetchUpdateOrderStatus: (
    orderId: string,
    newStatus: string,
    userId: string
  ) => dispatch(fetchUpdateOrderStatus(orderId, newStatus, userId)),
});

export default connect(mapStateToProps, mapDispatchToProps)(OrderItemContainer);
